import React from "react";
import { Route, Switch } from "react-router-dom";
import JustForFun from "./components/justForFun";
import BookLook from "./components/bookLook";

import Current from "./components/current";

export default function Routes() {
  return (
    <Switch>
      <Route exact path="/" component={Current}></Route>
      <Route exact path="/funIdeas" component={JustForFun}></Route>
      <Route exact path="/bookLook" component={BookLook}></Route>
      <Route>
        <div>Sorry Captain 404 not found</div>
      </Route>
    </Switch>
  );
}
