import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import "./components.css";
import JourneyToWest from "../img/JourneyToWest.png";
import Poisonwood from "../img/Poisonwood.png";
import EmmaReyes from "../img/EmmaReyes.png";

import FingerMonkey from "../img/fingerMonkey.gif";
const BookLook = () => {
  return (
    <div className="main_container">
      <div className="item">
        <div className="img">
          <img src={JourneyToWest} alt="Jouney to West"></img>
        </div>
        <div className="des">
          <div className="br">Journey To the West</div>
          <ul>
            Key Elements:
            <li>Cloud, Energy</li>
          </ul>
          <ul>
            Why this book:
            <li>
              {" "}
              A classic literary masterpiece that embodies the rich mythology,
              religious beliefs, and historical values of Chinese culture.
            </li>
          </ul>
          <ul>
            Inspiration:
            <li>
              This outfit, inspired by the Monkey King in the book "Journey to
              the West", tries to capture the essence of his vibrant, energetic
              nature and his ability to fly on clouds.
              <li>
                {" "}
                A vibrant color scheme with shades of gold, red, and royal blue
                is chosen to reflect his dynamic and powerful personality.
              </li>
              <li>
                Modern materials with traditional patterns (the cloud motifs),
                stylish cuts that maintain an echo of the traditional
                silhouette, can make the outfit fashionable, accessible and
                comfortable.
              </li>
              <li>
                At the end, complete the look with agile footwear to reflect the
                Monkey's adventurous nature.
              </li>{" "}
              This outfit would blend historical elements with imaginative
              details, capturing the essence of the Monkey's magical journey and
              vivacious character.
            </li>
          </ul>
          <ul>
            Technology used:
            <li>Character Design, Adobe Illustrator, Photoshop</li>
          </ul>
        </div>
      </div>
      <div className="item">
        <div className="img">
          <img src={EmmaReyes} alt="Poisonwood Bible"></img>
        </div>
        <div className="des">
          <div className="br">The Book of Emma Reyes</div>
          <ul>
            Key Elements:
            <li> Embroidery, fringes </li>
          </ul>
          <ul>
            Why this book:
            <li>
              Its vivid and poignant epistolary memoir offers a unique glimpse
              into the early 20th-century Colombian society through the eyes of
              a remarkable artist who overcame an impoverished and challenging
              childhood.
            </li>
          </ul>
          <ul>
            Inspiration:
            <li>
              Emma Reyes was a Colombian painter and writer whose letters
              detailed her harrowing childhood and youth in poverty before
              becoming a celebrated artist. The letters are characterized by
              their vivid, compelling, and straightforward writing style, which
              inspired the bold and vibrant colors in the illustration.
            </li>
            <li>
              The outfit highlights the element "Embroidery", which plays a
              vital role in Emma's teenager life and her art works.
            </li>
            <li>
              Incorporating elements of traditional Colombian clothing,
              including embroidered details and fringes pays homage to the
              historical and cultural backdrop of her story. The overall look
              would embody a blend of traditional and artistic flair, mirroring
              Emma's transformation and her vivid storytelling style.
            </li>
            <li>
              The outfit blends her artistic flair and yet is simple and
              elegant, reflecting her feminine strengthin her journey from a
              difficult childhood to a celebrated artist,
            </li>
          </ul>
          <ul>
            Technology used:
            <li> Photoshop, Adobe Illustrator</li>
          </ul>
        </div>
      </div>

      <div className="item">
        <div className="img">
          <img src={Poisonwood} alt=" Process for helping others"></img>
        </div>
        <div className="des">
          <div className="br">Poisonwood Bible</div>
          <ul>
            Key Elements:
            <li>Jungle prints, 60s Pants, Recycled Materials</li>
          </ul>
          <ul>
            Why this book:
            <li>
              It vividly portrays the complexities of Congolese culture,
              politics, and the impact of Western colonialism through the
              experiences of an American missionary family in the Congo during
              the 1960s.
            </li>
          </ul>
          <ul>
            Inspiration:
            <li>
              Inspired by the character Orleanna Price, the outfit incorporate
              elements that blend American and African influences, reflecting
              the novel's exploration of cultural interactions. This could be
              seen in the use of African textiles or accessories with 60s
              American clothing styles.
            </li>
            <li>
              Congo's symbolic vibrant prints, such as leaf prints, animal
              patterns, or vibrant floral designs, are embodied to reflect the
              untamed beauty and complexity of the African wilderness.
            </li>
          </ul>
          <ul>
            Technology used:
            <li> Photoshop, Adobe Illustrator</li>
          </ul>
        </div>
      </div>
    </div>
  );
};
export default BookLook;
